import { Button } from "@mui/material";
import { ReactNode } from "react";

interface IProps {
  children: ReactNode;
  style?: any;
  type?: "button" | "submit" | "reset" | undefined;
}
function ButtonPrimary(props: IProps) {
  const { children, style, type } = props;
  return (
    <Button
      color="primary"
      sx={{
        width: "121px",
        height: "52px",
        border: "0",
        borderRadius: "50px",
        fontWeight: 400,
        fontSize: "1.4rem",
        ...style,
      }}
      type={type}
      variant="contained"
      {...props}
    >
      {children}
    </Button>
  );
}

export default ButtonPrimary;
