import { Stack } from "@mui/material";

function Wrapper(props: any) {
  const { children, hasLeftPadding, style } = props;
  return (
    <Stack
      sx={{
        paddingTop: "100px",
        paddingLeft: hasLeftPadding ? "200px" : "0",
        height: "100vh",
        scrollSnapAlign: "center",
        ...style,
      }}
    >
      {children}
    </Stack>
  );
}

export default Wrapper;
