import { Button } from "@mui/material";
import { Box } from "@mui/system";

function OverlayButton(props: any) {
  const { handleClick, boxStyle, buttonStyle, children } = props;
  return (
    <Box
      sx={{
        position: "absolute",
        top: 0,
        right: 0,
        left: 0,
        bottom: 0,
        background: "#000000a1",
        display: "flex",
        alignItems: "center",
        zIndex: 11,
        justifyContent: "center",
        ...boxStyle,
      }}
    >
      <Button onClick={handleClick} sx={{ ...buttonStyle }}>
        {children}
      </Button>
    </Box>
  );
}

export default OverlayButton;
