// TabsSection.tsx
import { Tab, Tabs } from "@mui/material";
import React, { useState } from "react";
import palette from "theme/palette";
import ReadyTabNavbar from "./ReadyTabNavbar";
import TabPanel from "./TabPanel";

function TabsSection() {
  const [value, setValue] = useState(0);

  const handleChange = (event: React.SyntheticEvent, newValue: number) => {
    setValue(newValue);
  };

  return (
    <>
      <Tabs
        aria-label="footer tabs"
        onChange={handleChange}
        sx={{
          color: palette.primary.main,
          maxWidth: "230px",
          borderBottom: `1px solid ${palette.primary.main}`,
        }}
        value={value}
      >
        <Tab label="Ready" sx={{ color: palette.primary.main }} />
        <Tab label="New Project" sx={{ color: palette.primary.main }} />
      </Tabs>
      {/* <Divider sx={{ borderColor: palette.primary.main, maxWidth: "270px" }} /> */}
      <TabPanel index={0} value={value}>
        <ReadyTabNavbar />
      </TabPanel>
      <TabPanel index={1} value={value}>
        Content for New Project tab
      </TabPanel>
    </>
  );
}

export default TabsSection;
