import { Typography } from "@mui/material";
import { makeStyles } from "@mui/styles";
import { Box, Stack } from "@mui/system";
import BathroomIcon from "assets/icons/bathroom";
import BedIcon from "assets/icons/bed";
import CropIcon from "assets/icons/crop";

const useStyles: any = makeStyles((theme: any) => ({
  iconBox: { display: "flex", alignItems: "center", gap: "5px" },
}));

function Card(props: any) {
  const { data, hasDescription, overlay, cardStyle, contentStyle, imgHeight } =
    props;
  const classes = useStyles();
  return (
    <Stack
      key={data.id}
      sx={{
        position: "relative",
        width: "100%",
        "& img": { width: "100%", height: imgHeight },
        ...cardStyle,
      }}
    >
      {overlay}
      <Box sx={{ position: "relative" }}>
        <img alt={data.title} src={data.img} />
      </Box>
      {hasDescription && (
        <Box sx={{ padding: "10px", paddingTop: "20px", ...contentStyle }}>
          <Typography>{data.title}</Typography>
          <Typography>{data.description}</Typography>
          <Stack alignItems="center" direction="row" gap="5px">
            <Box className={`${classes.iconBox}`}>
              <BedIcon />
              <Typography>{data.bedRoom}</Typography>
            </Box>
            <Box className={`${classes.iconBox}`}>
              <BathroomIcon />
              <Typography>{data.bathroom}</Typography>
            </Box>
            <Box className={`${classes.iconBox}`}>
              <CropIcon />
              <Typography>{data.area} sqrt</Typography>
            </Box>
          </Stack>
          <Typography>AED {data.price}</Typography>
        </Box>
      )}
    </Stack>
  );
}

export default Card;
