import {
  FormControl,
  FormLabel,
  InputLabel,
  MenuItem,
  Select,
  SelectChangeEvent,
  TextField,
  TextFieldVariants,
} from "@mui/material";
import { ChangeEvent, FocusEvent } from "react";
import palette from "theme/palette";

type InputFieldProps<T> = {
  label?: string;
  value: "" | { value: unknown } | undefined;
  onTextChange?: (value: string) => void; // New onChange handler for TextField
  onSelectChange?: (value: T) => void; // New onChange handler for Select
  onBlur?: (event: FocusEvent<HTMLInputElement>) => void;
  onFocus?: (event: FocusEvent<HTMLInputElement>) => void;
  type?: string | "text";
  options?: { value: any; label: string }[];
  name?: string;
  variant?: TextFieldVariants | undefined;
  required?: boolean;
  placeholder?: string;
  style?: any;
};

function InputField<T>(props: InputFieldProps<T>) {
  const {
    label,
    value,
    onTextChange,
    onSelectChange,
    type,
    options,
    name,
    variant,
    required,
    onBlur,
    onFocus,
    placeholder,
    style,
  } = props;

  const handleTextChange = (event: ChangeEvent<HTMLInputElement>) => {
    if (onTextChange) {
      onTextChange(event.target.value);
    }
  };

  const handleSelectChange = (event: SelectChangeEvent<{ value: unknown }>) => {
    if (onSelectChange) {
      onSelectChange(event.target.value as T);
    }
  };

  if (type === "select" && options) {
    return (
      <FormControl fullWidth>
        {label && (
          <FormLabel
            htmlFor={name}
            sx={{
              color: "inherit",
              fontSize: {
                xs: "12px",
                md: "14px",
                xl: "1rem",
                lg: "13px",
                fontWeight: "500",
              },
              marginTop: "5px",
              textTransform: "capitalize",
            }}
          >
            {label}
            {required ? (
              <span style={{ color: palette.error.main }}>*</span>
            ) : null}
          </FormLabel>
        )}
        <InputLabel>{label}</InputLabel>
        <Select
          onBlur={onBlur}
          onChange={handleSelectChange} // Use handleSelectChange for onChange
          onFocus={onFocus}
          placeholder={placeholder}
          sx={{ ...style }}
          value={value}
          variant={variant}
        >
          {options.map((option) => (
            <MenuItem key={option.value} value={option.value}>
              {option.label}
            </MenuItem>
          ))}
        </Select>
      </FormControl>
    );
  }

  return (
    <TextField
      fullWidth
      label={label}
      onBlur={onBlur}
      onChange={handleTextChange} // Use handleTextChange for onChange
      onFocus={onFocus}
      placeholder={placeholder}
      required={required}
      sx={{
        color: palette.primary.main,
        borderColor: palette.primary.main,
        fontFamily: "Helvetica Neue,sans-serif",
        "&::placeholder": {
          color: palette.primary.main,
          fontSize: "1.4rem",
          fontFamily: "Helvetica Neue,sans-serif",
        },
        "&::before": {
          borderBottom: `1px solid ${palette.primary.main}`,
        },
        ...style,
      }}
      type={type}
      value={value}
      variant={variant}
    />
  );
}

export default InputField;
