import {
  Box,
  Button,
  ButtonGroup,
  Grid,
  Stack,
  Typography,
} from "@mui/material";
import { makeStyles } from "@mui/styles";
import { propertyData } from "components/__mock__/card";
import Card from "components/Card";
import OverlayButton from "components/OverlayButton";
import Wrapper from "components/Wrapper";
import { useState } from "react";
import palette from "theme/palette";

const useStyles: any = makeStyles((theme: any) => ({
  iconBox: { display: "flex", alignItems: "center", gap: "5px" },
  buttonRoot: {
    backgroundColor: "transparent",
    border: "none",
    borderBottom: "3px solid transparent",
    borderRadius: 0,
    "&:hover": {
      border: "none",
      backgroundColor: "transparent",
      borderBottom: `3px solid ${palette.primary.main}`,
    },
    "&.active": {
      border: "none",
      borderBottom: `3px solid ${palette.primary.main}`,
    },
  },
}));

function PropertiesSection() {
  const classes = useStyles();
  const [selectedButton1, setSelectedButton1] = useState<string | null>(null);
  const [selectedButton2, setSelectedButton2] = useState<string | null>(null);

  const handleButton1Click = (buttonName: string) => {
    setSelectedButton1(buttonName);
  };
  const handleButton2Click = (buttonName: string) => {
    setSelectedButton2(buttonName);
  };
  return (
    <Wrapper hasLeftPadding>
      <Stack sx={{ marginLeft: "20px", pt: "200px" }}>
        <Stack
          alignItems="flex-end"
          direction="row"
          // sx={{ paddingBottom: "40px" }}
        >
          <Typography
            component="h2"
            sx={{ maxWidth: "386px", lineHeight: "1" }}
            variant="h1"
          >
            Featured Properties
          </Typography>
          <Box>
            <ButtonGroup
              sx={{
                background: "transparent",
                borderBottom: `1px solid ${palette.primary.main}`,
                borderRadius: "0px",
                display: "block",
                marginBottom: "10px",
              }}
              variant="outlined"
            >
              <Button
                className={`${classes.buttonRoot} ${
                  selectedButton1 === "villa" ? "active" : ""
                }`}
                onClick={() => handleButton1Click("villa")}
              >
                Villa
              </Button>
              <Button
                className={`${classes.buttonRoot} ${
                  selectedButton1 === "apartment" ? "active" : ""
                }`}
                onClick={() => handleButton1Click("apartment")}
              >
                Apartment
              </Button>
              <Button
                className={`${classes.buttonRoot} ${
                  selectedButton1 === "viewAll" ? "active" : ""
                }`}
                onClick={() => handleButton1Click("viewAll")}
              >
                View All
              </Button>
            </ButtonGroup>
            <ButtonGroup
              sx={{
                background: "transparent",
                borderBottom: `1px solid ${palette.primary.main}`,
                borderRadius: "0px",
                display: "block",
              }}
              variant="outlined"
            >
              <Button
                className={`${classes.buttonRoot} ${
                  selectedButton2 === "rent" ? "active" : ""
                }`}
                onClick={() => handleButton2Click("rent")}
              >
                Rent
              </Button>
              <Button
                className={`${classes.buttonRoot} ${
                  selectedButton2 === "buy" ? "active" : ""
                }`}
                onClick={() => handleButton2Click("buy")}
              >
                Buy{" "}
              </Button>
              <Button
                className={`${classes.buttonRoot} ${
                  selectedButton2 === "sell" ? "active" : ""
                }`}
                onClick={() => handleButton2Click("sell")}
              >
                Sell
              </Button>
            </ButtonGroup>
          </Box>
        </Stack>
        <Stack
          alignItems="flex-start"
          direction="row"
          gap="0"
          sx={{ marginRight: "-32px" }}
        >
          <Grid container>
            {propertyData?.map((item, index) => {
              if (index === 3) {
                return (
                  <Grid lg={3}>
                    <Card
                      key={item.id}
                      hasDescription
                      x
                      data={item}
                      imgHeight="293px"
                      overlay={
                        <OverlayButton handleClick={() => {}}>
                          Explore More
                        </OverlayButton>
                      }
                    />
                  </Grid>
                );
              }
              return (
                <Grid lg={3}>
                  <Card
                    key={item.id}
                    hasDescription
                    data={item}
                    imgHeight="293px"
                  />
                </Grid>
              );
            })}
          </Grid>
        </Stack>
      </Stack>
    </Wrapper>
  );
}

export default PropertiesSection;
